import React, {Component} from 'react';
import './SettingMenu.css';
import Resize from './Resize/Resize';
import ExportGif from './Export/ExportGif';

export default class SettingMenu extends Component {

    onExport = () => {
        ExportGif();
    }
    componentDidMount() {
        this.onExport();
    }

    render() {
        return (
            <div className="settings__container">
                <div className="setting__hidden">
                    <Resize/>
                    <button className="button button__header button__export">Export as gif</button>
                    <a className="link__save" href="1" download></a>
                    <button className="button__keyboard"></button>
                </div>       
            </div>
        );
    }      
}
