import React, {Component} from 'react';
import './tools.css';
import paintBucket from './ToolsFunction/PaintBucket';
import Stroke from './ToolsFunction/Stroke';
import PaintAll from './ToolsFunction/PaintAllPixel';
import Rectangle from './ToolsFunction/Rectangle';
import Circle from './ToolsFunction/Circle';
import PenMirror from './ToolsFunction/PenMirror';
import Dithering from './ToolsFunction/Dithering';
import Lightening from './ToolsFunction/Lighening';
import Move from './ToolsFunction/Move';
import ColorPicker from './ToolsFunction/ColorPicker';
import Pen from './ToolsFunction/Pen';
import Eraser from './ToolsFunction/Eraser';
import Rotate from '../TransformTools/tools/Rotate';
import Flip from '../TransformTools/tools/Flip';
import Modal from '../../components/ModalKeyboard/Modal';


export default class  Tools extends Component {
    
    state = {
        activeTool: '',
        shortkats: {
            Pen: 'KeyP',
            MirrorPen: 'KeyI',
            PaintBucket: 'KeyB',
            PaintAll: 'KeyA',
            Eraser: 'KeyE',
            Stroke: 'KeyS',
            Rectangle: 'KeyR',
            Circle: 'KeyC',
            Move: 'KeyM',
            Lightening: 'KeyL',
            Dithering: 'KeyD',
            ColorPicker: 'KeyO',
            Flip: 'KeyF',
            Rotate: 'KeyT'
        },
        isOpen: false,
    }

    onChoseTool() {
        const tools = document.querySelector('.tools__wrapper');
        tools.addEventListener('click', this.choseTool, false);
        document.addEventListener("contextmenu", function(e){
            e.preventDefault();
        }, false);
    }

    changeActiveTool(e) {
        const target = e.target;
        if(this.state.activeTool !== '' && target.tagName === 'LI') {
            this.state.activeTool.classList.remove('tool--active');
        }
        if (target.tagName === 'LI') {
            e.target.classList.toggle('tool--active');   
        }  
        this.setState({
            activeTool: e.target,
        });    
    }

    choseTool = (e) => {
        const target = e.target;
        if(target.classList.contains('tool__pen')) {
            if (this.state.activeTool !== e.target) {
                this.changeActiveTool(e);  
                Pen();                              
            }            
        }
        else if(target.classList.contains('tool__paint--bucket')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);
                paintBucket();                
            }         
        }
        else if(target.classList.contains('tool__eraser')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Eraser();                            
            }         
        }
        else if(target.classList.contains('tool__stroke')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Stroke();                            
            }         
        }
        else if(target.classList.contains('tool_paint--all')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                PaintAll();                            
            }         
        }
        else if(target.classList.contains('tool__rectangle')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Rectangle();                            
            }         
        }
        else if(target.classList.contains('tool__circle')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Circle();                            
            }         
        }
        else if(target.classList.contains('tool__pen-mirror')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                PenMirror();                            
            }         
        }
        else if(target.classList.contains('tool__dithering')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Dithering();                            
            }         
        }
        else if(target.classList.contains('tool__lighten')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Lightening();                            
            }         
        }
        else if(target.classList.contains('tool__move')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                Move();                            
            }         
        }
        else if(target.classList.contains('tool__color--picker')) {
            if(this.state.activeTool !== e.target) {
                this.changeActiveTool(e);   
                ColorPicker();                          
            }         
        }
    }

    onToolShortcats = (func,tool) => {
            const aTool = document.querySelector(tool);
            if (this.state.activeTool !== aTool) {
                if (this.state.activeTool !== '') {
                    this.state.activeTool.classList.remove('tool--active');
                }
                aTool.classList.add('tool--active');
                this.setState ({
                    activeTool: aTool,
                })
                func();
            }        
    }

    shortcats = (e) => {
        if (e.code === this.state.shortkats.Pen) {
            this.onToolShortcats(Pen, '.tool__pen');
        } else if (e.code ===  this.state.shortkats.MirrorPen) {
            this.onToolShortcats(PenMirror, '.tool__pen-mirror');
        } else if (e.code ===  this.state.shortkats.PaintBucket) {
            this.onToolShortcats(paintBucket, '.tool__paint--bucket');     
        } else if (e.code ===  this.state.shortkats.Eraser) {
            this.onToolShortcats(Eraser, '.tool__eraser');
        } else if (e.code ===  this.state.shortkats.Stroke) {
            this.onToolShortcats(Stroke, '.tool__stroke');
        } else if (e.code ===  this.state.shortkats.PaintAll) {
            this.onToolShortcats(PaintAll, '.tool_paint--all');
        } else if (e.code ===  this.state.shortkats.Rectangle) {
            this.onToolShortcats(Rectangle, '.tool__rectangle');
        } else if (e.code ===  this.state.shortkats.Circle) {
            this.onToolShortcats(Circle, '.tool__circle');
        } else if (e.code ===  this.state.shortkats.Dithering) {
            this.onToolShortcats(Dithering, '.tool__dithering');
        } else if (e.code ===  this.state.shortkats.Lightening) {
            this.onToolShortcats(Lightening, '.tool__lighten');
        } else if (e.code ===  this.state.shortkats.Move) {
            this.onToolShortcats(Move, '.tool__move');
        } else if (e.code ===  this.state.shortkats.ColorPicker) {
            this.onToolShortcats(ColorPicker, '.tool__color--picker');
        } else if (e.code ===  this.state.shortkats.Rotate) {
           Rotate();
        } else if (e.code ===  this.state.shortkats.Flip) {
           Flip();
        }
    }

    onShortcats = () => {
        document.addEventListener('keydown', this.shortcats, false);
    }

    openModal = () => {
        this.setState({ isOpen: true });
    }
    
    handleSubmit = () => {
    this.setState({ isOpen: false });
    }
    
    handleCancel = () => {
    this.setState({ isOpen: false });
    }
    
    handleClick = () => {
        const b = document.querySelector('.button__keyboard');
        b.addEventListener('click', () => {
            this.setState({
                isOpen: true,
            })
        }, false);
    }

    renderItem = (arr) => {
        return arr.map(item => {
            return (
                {}
            )
        })
    }

    componentDidMount() {
        this.onChoseTool();
        this.onShortcats();
        this.handleClick();
    }
    componentWillUnmount() {
        this.onChoseTool = () => {};
    }

    render() {
        // const items = this.renderItem(items);

        return (
            <ul className="tools__wrapper">
                <li className="tool tool__pen" title="Pen"></li>
                <li className="tool tool--fullsize tool__pen-mirror" title="Mirror pen"></li>
                <li className="tool tool__paint--bucket" title="Paint bucket"></li>
                <li className="tool tool--fullsize tool_paint--all" title="Paint all pixel of the same color"></li>
                <li className="tool tool__eraser" title="Eraser"></li>
                <li className="tool tool--fullsize tool__stroke" title="Stroke"></li>
                <li className="tool tool__rectangle" title="Rectangle"></li>
                <li className="tool tool__circle" title="Circle"></li>
                <li className="tool tool__move" title="Move"></li>
                <li className="tool tool_shape hidden" title="Shape"></li>
                <li className="tool tool__rectangle--selection hidden" title="Rectangle-selection"></li>
                <li className="tool tool__lasso hidden" title="lasso"></li>
                <li className="tool tool__lighten" title="Lightening"></li>
                <li className="tool tool__dithering" title="Dithering"></li>
                <li className="tool tool__color--picker" title="Color picker"></li>  
                <Modal
                    title="Keyboard Shortcats"
                    isOpen={this.state.isOpen}
                    onCancel={this.handleCancel}
                    onSubmit={this.handleSubmit}
                />
            </ul>
        );
    };
}
