import GIF from 'gif.js.optimized';

export default function ExportGif() {
    const saveBut = document.querySelector('.button__export');
    const xx = () => {     
        const range = document.querySelector('.fps__range');
        const frameCanvas = document.querySelectorAll('.frame');
        const save = document.querySelector('.link__save');
        save.innerHTML = '';
        const gif = new GIF({
            workers: 2,
            quality: range.value,
            workerScript: './gif.worker.js', 
            repeat: 0,
            width: frameCanvas[0].width,
            height: frameCanvas[0].height,
        });
        for (let i = 0; i < frameCanvas.length; i += 1) {
            gif.addFrame(frameCanvas[i], { delay: range.value });
        }
        gif.on('finished', (blob) => {
            save.href = URL.createObjectURL(blob);
            save.innerHTML = 'Download';
        });
        gif.render();
    }
    saveBut.addEventListener('click', xx, false);
}
